export default {
  name: "Contact",
  transition: "page",
  head () {
    return {
      title: 'Kat Lightfoot - Contact',
    }
  },
  async asyncData({ $prismic, error }) {
  try {

    const document = (await $prismic.api.getSingle('contact')).data

    return {
      document
    };
  } catch (e) {
    error({ statusCode: 404, message: "Page not found" });
  }
}
}
